@use "styles/base";

* {
  //   outline: 1px solid red;
}
.admin-container {
  margin: 0;
  padding: 0;
  width: 80%;
  margin: 10%;
  h1 {
    color: base.$darkgreen;
  }

  .admin-child-container {
    background-color: base.$darkgreen;
    width: 100%;
    color: white;
    border-radius: 5px;
  }
  .feature {
    display: flex;
    align-items: center;
    padding: 5px 5%;
    height: 50px;
    p {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;

      border-bottom: 1px solid gray;
    }
  }
  .feature-container {
    background-color: white;
  }
  .admin-page-icon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
}
