@use "styles/base";

.home {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 100px;
  width: 100%;
  top: 0;
  .top-container {
    display: block;
    position: relative;
    width: 100%;
    // outline: 1px solid red;
    .img-background {
      object-fit: cover;
      width: 100%;
    }
    .photo-container {
      width: 25%;
      position: absolute;
      top: 10%;
      left: 7%;
      padding: 10px;
      background-color: white;
      outline: 1px solid rgba(128, 128, 128, 0.685);
      .img-photo {
        object-fit: cover;
        width: 100%;
      }
    }
    h1 {
      font-family: "Alex Brush", cursive;
      color: base.$red;
      width: 90%;
      margin-left: 5%;
      text-align: center;
      position: absolute;
      top: 0;
      right: 7%;
      width: 50%;
      height: 100%;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .table-info-container {
      display: flex;
      position: absolute;
      justify-content: space-around;
      width: 80%;

      bottom: 0;
      margin-left: 10%;
      //   outline: 1px solid green;
      p {
        margin: 0;
        padding: 0;
        color: base.$darkgreen;
        font-size: 0.8em;
        b {
          color: base.$orange;
        }
      }
    }
  }

  .tabs-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    height: 30px;
    margin-left: 10%;
    margin-top: 10px;
    margin-bottom: 10px;
    .child-tab {
      width: 50%;
      opacity: 0.5;
    }
    .active-tab {
      color: base.$green;
      font-weight: bold;
      opacity: 1;
      p {
        border-bottom: 2px solid base.$red;
        width: fit-content;
      }
    }
  }
  .home-search-container {
    width: 80%;
    margin-left: 10%;
    position: relative;
    .search-container {
      display: flex;
      position: relative;
      align-items: center;
      justify-content: center;
      .search-icon {
        position: absolute;
        width: 25px;
        height: 25px;
        opacity: 0.2;
        left: 2%;
      }
      .delete-icon {
        position: absolute;
        width: 25px;
        height: 25px;
        right: 2%;
        color: base.$red;
      }
      input {
        width: 100%;
        height: 30px;
        padding: 5px 40px;
        border-radius: 10px;
        border-color: rgba(128, 128, 128, 0.534);
        font-size: 1em;
        outline: none;
        &:focus {
          outline: none;
          box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px;
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
    }
    p {
      font-size: 10px;
      font-weight: bold;
      opacity: 0.5;
    }
    .results-container {
      box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      margin: 0;
      padding: 0;
      display: block;
      position: absolute;
      width: 100%;
      background-color: white;
      z-index: 5;
      h3 {
        margin: 10px 5% 10px 5%;
        opacity: 0.5;
        font-size: 0.8em;
      }
      ul {
        list-style-type: none;
        width: 90%;
        margin: 0 5%;
        padding: 0;

        li {
          display: block;
          position: relative;
          border-top: 1px solid gray;
          padding: 10px 5px;
          span {
            display: block;
            width: 100%;
            font-size: 20px;
          }
          .extra-info {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
        }
        .search-result-attend-guest {
          background-color: rgba(158, 158, 158, 0.103);
          color: base.$darkgreen;
          .search-result-text-highlight {
            opacity: 1 !important;
          }
        }
      }
    }
  }
  .home-groups-container {
    display: block;
    .child-group {
      margin-bottom: 20px;
      margin-right: 20px;
    }
  }
  .selected-guests {
    width: 80%;
    margin-left: 10%;
    background-color: white;
    z-index: 5;
    h3 {
      opacity: 0.5;
    }
    table {
      font-family: Arial, Helvetica, sans-serif;
      border-collapse: collapse;
      width: 100%;
      td,
      th {
        border: 1px solid #ddd;
        padding: 8px;
        position: relative;
      }

      tr:nth-child(odd) {
        // background-color: #f2f2f2;
      }

      tr:hover {
        // background-color: #ddd;
      }

      th {
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
        background-color: base.$green;
        color: white;
      }
      .column-highlight {
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
      }
      .quality-guest {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 100px;
        .icon {
          width: 40px;
          height: 40px;
        }
        .icon-minus {
          color: base.$red;
        }
        .icon-plus {
          color: base.$green;
        }
        .text {
          font-weight: bold;
          font-size: 2em;
          text-align: center;
          color: base.$darkgreen;
        }
      }
      .note {
        font-size: 10px;
        position: absolute;
        right: 2%;
        top: 50%;
      }
    }
    button {
      margin-top: 20px;
    }
  }
}
