@use "styles/base";

.burger-menu-icon {
  position: absolute;
  right: 5%;
  top: 5%;
  z-index: 5;
}
.burger-menu-container {
  position: fixed;
  width: 50%;
  height: 100vh;
  top: 0;
  right: 0;
  z-index: 9;
  animation: menuAnimation 0.5s ease-in-out;
  .cover {
    position: absolute;
    background-color: black;
    width: 100%;
    height: 100%;
    opacity: 0.9;
  }
  .burger-menu-icon-close {
    position: absolute;
    top: 2%;
    right: 7%;
    color: base.$red;
  }
  ul {
    position: relative;
    width: 80%;
    height: 50%;
    left: 0;
    margin: 0;
    padding: 0;

    li {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 25%;
    }
  }
  a {
    font-family: "Charm", cursive;
    color: white;
    text-decoration: none;
  }
}
@keyframes menuAnimation {
  from {
    right: -50%;
  }
  to {
    right: 0;
  }
}
